/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import { Controller, RenderContentProps } from '@ali/4ever-cangjie';
import type { SelectManager } from './utils/SelectManager';
import { MultipleSelectToolbar } from './components/MultipleSelectToolbar';
import { INodeSelectPluginConfigs } from './types';

export default (config: INodeSelectPluginConfigs, selectManager: SelectManager) => {
  const { toolbarItemsPriority } = config || {};
  return (props: RenderContentProps, controller: Controller, next) => {
    return (
      <React.Fragment>
        {next()}
        <MultipleSelectToolbar controller={controller} manager={selectManager} toolbarItemsPriority={toolbarItemsPriority}/>
      </React.Fragment>
    );
  };
};
